import React, {useEffect, useState} from 'react';

function Activities()
{
    const [dropboxState, setDropboxState] = useState(false);
    const [dropbox, setDropbox] = useState(() => {
        return <div></div>;
    })

    useEffect(() => {
        console.log("USE EFFECT ", dropboxState)
        if (dropboxState)
        {
            setDropbox(<header>
                <nav>
                    <ul1>
                        <li><a href="/axs" class="hover-underline-animation">HOME</a></li>
                        <li><a href="/rush" class="hover-underline-animation">RUSH</a></li>
                        <li><a href="/people" class="hover-underline-animation">PEOPLE</a></li>
                        <li><a href="https://axe-beta-gamma.square.site/" class="hover-underline-animation">STORE</a></li>
                        <li><a href="https://my.setmore.com/bookingpage/97afc1fa-6977-4e61-98ee-293d35943cf1" class="hover-underline-animation">TUTORING</a></li>
                        <li><a href="/contact" class="hover-underline-animation">CONTACT</a></li>
                    </ul1>
                </nav>
            </header>)
        }
        else
        {
            setDropbox(<div></div>);
        }
    }, [dropboxState])

    function toggleDropboxState()
    {
        setDropboxState(!dropboxState);
    }
    const [slideNumber, changeNumber] = useState(0)
    const [slideName, changeName] = useState(() => {
        return <img class="imageLoad" id="EYU" src="IMG_1722.jpg" alt="EYU Fair"/>
    })

    useEffect(() => {
        console.log("USE EFFECT ", slideNumber)
        if ((slideNumber === 0))
        {
            changeName(slideName => <img class="imageLoad" id="EYU" src="IMG_1722.jpg" alt="EYU Fair"/>) 
        }
        else if ((slideNumber === 1))
        {
            changeName(changeName => <img class="imageLoad" id="EYU1" src="IMG_1723.jpg" alt="EYU Fair"/>)
        }
        else if ((slideNumber === 2))
        {
            changeName(changeName => <img class="imageLoad" id="EYU2" src="IMG_1725.jpg" alt="EYU Fair"/>)
        }
        /*
        else if((slideNumber === 3))
        {
            changeName(changeName => <img class="imageLoad" id="EYU" src="_DSC0789.jpg" alt="Seaborg Award"/>)
        }
        */
    }, [slideNumber])

    function incrementNumber()
    {
        if (slideNumber === 2)
        {
            changeNumber(0);
        }
        else
        {
            changeNumber(prevSlideNumber => prevSlideNumber + 1);
        }
    }

    function decrementNumber()
    {
        if (slideNumber === 0)
        {
            changeNumber(2);
        }
        else
        {
            changeNumber(prevSlideNumber => prevSlideNumber - 1);
        }
    }
    /*<h7 class="backLinkHome"><a href="javascript:history.back()" class="whiteLink">&#10094; BACK TO HOME</a></h7>*/
    return (
        <>
            <button onClick={toggleDropboxState} class="toggle-button">
                <span class="bar"></span>
                <span class="bar"></span>
                <span class="bar"></span>
            </button>
            {dropbox}
            <link rel="stylesheet" href="styles.css"/>
            <header class="navOverlayImage">
                <nav>
                    <ul>
                        <li><a href="/axs" class="hover-underline-animation">HOME</a></li>
                        <li><a href="/rush" class="hover-underline-animation">RUSH</a></li>
                        <li><a href="/people" class="hover-underline-animation">PEOPLE</a></li>
                        <li><a href="https://axe-beta-gamma.square.site/" class="hover-underline-animation">STORE</a></li>
                        <li><a href="https://my.setmore.com/bookingpage/97afc1fa-6977-4e61-98ee-293d35943cf1" class="hover-underline-animation">TUTORING</a></li>
                        <li><a href="/contact" class="hover-underline-animation">CONTACT</a></li>
                    </ul>
                </nav>
            </header>
            <h7 class="backLinkHome"><a href="javascript:history.back()" class="whiteLink">&#10094; BACK TO HOME</a></h7>
            <button class="prev" onClick={decrementNumber}>&#10094;</button>
            <button class="next" onClick={incrementNumber}>&#10095;</button>
            {slideName}
            <h8>Exploring Your Universe</h8>
            <br></br>
            <p8>
                At Alpha Chi Sigma, we strive to expand chemistry to a broader audience.
                Members at this fraternity volunteer both on campus and around the greater Los Angeles area to inspire prospective 
                chemists showing them more about the field and what it has to entail. Alpha Chi Sigma also
                maintains close ties with the Department of Chemistry and Biochemistry and is responsible 
                for hosting the annual Glenn T. Seaborg Banquet, sponsoring the award, as well as many others things at UCLA.
            </p8>
            <div class="activitiesWhitespace"></div>
            <div className="centerAll">
                <div class="socialMediaGrid" id="socialMediaGridExtraExtraSpace">
                    <div class="socialMedia1">
                        <h7 class="socialMediaPosition">
                            <a href="https://www.facebook.com/uclaaxs/" class="hover-underline-animation">FACEBOOK</a>
                        </h7>
                    </div>
                    <div class="socialMedia2">
                        <h7 class="socialMediaPosition">
                            <a href="https://www.instagram.com/uclaaxs/" class="hover-underline-animation">INSTAGRAM</a>
                        </h7>
                    </div>
                </div>
            </div>
        </>
    )
    /*https://www.w3schools.com/howto/howto_js_slideshow.asp*/
}

export default Activities;