import React, {useEffect, useState} from 'react';

function History()
{   
    const [dropboxState, setDropboxState] = useState(false);
    const [dropbox, setDropbox] = useState(() => {
        return <div></div>;
    })

    useEffect(() => {
        console.log("USE EFFECT ", dropboxState)
        if (dropboxState)
        {
            setDropbox(<header>
                <nav>
                    <ul1>
                        <li><a href="/axs" class="hover-underline-animation">HOME</a></li>
                        <li><a href="/rush" class="hover-underline-animation">RUSH</a></li>
                        <li><a href="/people" class="hover-underline-animation">PEOPLE</a></li>
                        <li><a href="https://axe-beta-gamma.square.site/" class="hover-underline-animation">STORE</a></li>
                        <li><a href="https://my.setmore.com/bookingpage/97afc1fa-6977-4e61-98ee-293d35943cf1" class="hover-underline-animation">TUTORING</a></li>
                        <li><a href="/contact" class="hover-underline-animation">CONTACT</a></li>
                    </ul1>
                </nav>
            </header>)
        }
        else
        {
            setDropbox(<div></div>);
        }
    }, [dropboxState])

    function toggleDropboxState()
    {
        setDropboxState(!dropboxState);
    }
    /*<h7 class="backLinkHome"><a href="javascript:history.back()"class="whiteLink">&#10094; BACK TO HOME</a></h7>*/
    return (
        <>
            <button onClick={toggleDropboxState} class="toggle-button">
                <span class="bar"></span>
                <span class="bar"></span>
                <span class="bar"></span>
            </button>
            {dropbox}
            <header>
                <nav>
                    <ul>
                        <li><a href="/axs" class="hover-underline-animation">HOME</a></li>
                        <li><a href="/rush" class="hover-underline-animation">RUSH</a></li>
                        <li><a href="/people" class="hover-underline-animation">PEOPLE</a></li>
                        <li><a href="https://axe-beta-gamma.square.site/" class="hover-underline-animation">STORE</a></li>
                        <li><a href="https://my.setmore.com/bookingpage/97afc1fa-6977-4e61-98ee-293d35943cf1" class="hover-underline-animation">TUTORING</a></li>
                        <li><a href="/contact" class="hover-underline-animation">CONTACT</a></li>
                    </ul>
                </nav>
            </header>
            <h7 class="backLinkHome"><a href="javascript:history.back()"class="whiteLink">&#10094; BACK TO HOME</a></h7>
            <div>
                <img class="imageLoad" id="founder" src="Founders-Collage.jpeg" alt="Founders"/>
                <h8>History of the Fraternity</h8>
                <br></br>
                <p8>
                    Alpha Chi Sigma is the only national co-educational, professional chemistry fraternity in the United States. 
                    We provide the catalyst to form lifelong friendships and connections among those who share a common interest 
                    in chemistry and related fields. The fraternity was founded at the University of Wisconsin at Madison in late 
                    1902 by a group of undergraduates who were fellow students in chemistry at that time. The Beta Gamma Chapter 
                    of Alpha Chi Sigma was founded on March 30, 1935. Since then, three members of the Beta Gamma chapter 
                    have received the nobel prize for their outstanding contribution to the chemical profession.
                </p8>
            </div>
            <div class="calendarWhitespace"></div>
            <div class="socialMediaGrid" id="socialMediaGridExtraExtraSpace">
                <div class="socialMedia1">
                    <h7 class="socialMediaPosition">
                        <a href="https://www.facebook.com/uclaaxs/" class="hover-underline-animation">FACEBOOK</a>
                    </h7>
                </div>
                <div class="socialMedia2">
                    <h7 class="socialMediaPosition">
                        <a href="https://www.instagram.com/uclaaxs/" class="hover-underline-animation">INSTAGRAM</a>
                    </h7>
                </div>
            </div>
        </>
    )
}

export default History;