import React, {useEffect, useState} from 'react';

export default function People()
{
    const [dropboxState, setDropboxState] = useState(false);
    const [dropbox, setDropbox] = useState(() => {
        return <div></div>;
    })

    useEffect(() => {
        console.log("USE EFFECT ", dropboxState)
        if (dropboxState)
        {
            setDropbox(<header>
                <nav>
                    <ul1>
                        <li><a href="/axs" class="hover-underline-animation">HOME</a></li>
                        <li><a href="/rush" class="hover-underline-animation">RUSH</a></li>
                        <li><a href="/people" class="hover-underline-animation">PEOPLE</a></li>
                        <li><a href="https://axe-beta-gamma.square.site/" class="hover-underline-animation">STORE</a></li>
                        <li><a href="https://my.setmore.com/bookingpage/97afc1fa-6977-4e61-98ee-293d35943cf1" class="hover-underline-animation">TUTORING</a></li>
                        <li><a href="/contact" class="hover-underline-animation">CONTACT</a></li>
                    </ul1>
                </nav>
            </header>)
        }
        else
        {
            setDropbox(<div></div>);
        }
    }, [dropboxState])

    function toggleDropboxState()
    {
        setDropboxState(!dropboxState);
    }
    return (
        <>
            <button onClick={toggleDropboxState} class="toggle-button">
                <span class="bar"></span>
                <span class="bar"></span>
                <span class="bar"></span>
            </button>
            {dropbox}
            <header class="navOverlayImage">
                <nav>
                    <ul>
                        <li><a href="/axs" class="hover-underline-animation">HOME</a></li>
                        <li><a href="/rush" class="hover-underline-animation">RUSH</a></li>
                        <li><a href="/people" class="hover-underline-animation">PEOPLE</a></li>
                        <li><a href="https://axe-beta-gamma.square.site/" class="hover-underline-animation">STORE</a></li>
                        <li><a href="https://my.setmore.com/bookingpage/97afc1fa-6977-4e61-98ee-293d35943cf1" class="hover-underline-animation">TUTORING</a></li>
                        <li><a href="/contact" class="hover-underline-animation">CONTACT</a></li>
                    </ul>
                </nav>
            </header>
            <img class="imageLoadDelay" id="AXSMembers" src="IMG_1856.jpg" alt="AXS Members"/>
            <div class="imageTitle" id="membersOfTheChemicalProfession">
                <h9>MEMBERS</h9>
                    <p9><br></br>of Beta Gamma Chapter</p9>
            </div>

            <img class="imageLoadDelay" id="undergraduateMembers" src="IMG_1599.jpg" alt="Undergraduate Members"/>
            <h13 class="imageTitle" id="undergraduateMembersTitle">Undergraduate Members
                <div class="learnMorePosition" id="undergraduateMembersLearnMore">
                    <h6 class="learnMoreBox"><a href="/undergraduate" class="hover-highlight-animation">Learn More</a></h6>
                </div>
            </h13>
            
            <img class="imageLoadDelay" id="pledgeClass" src="IMG_1611.jpg" alt="Pledge Class"/>
            <h13 class = "imageTitle" id="pledgeClassTitle">Pledge Classes
                <div class = "learnMorePosition" id="pledgeClassLearnMore">
                    <h6 class="learnMoreBox"><a href="/pledge" class="hover-highlight-animation">Learn More</a></h6>
                </div>
            </h13>
            
            <img class="imageLoadDelay" id="professorMembers" src="professor.jpg" alt="Professor Members"/>
            <h13 class = "imageTitle" id="professorMembersTitle">Professor Members
                <div class="learnMorePosition" id="professorMembersLearnMore">
                    <h6 class="learnMoreBox"><a href="/professor" class="hover-highlight-animation">Learn More</a></h6>
                </div>
            </h13>

            <img class="imageLoadDelay" id="alumniMembers" src="289458670_1037231557165581_8342686701647794390_n.jpg" alt="Alumni Members"/>
            <h13 class = "imageTitle" id="alumniMembersTitle">Alumni Members
                <div class ="learnMorePosition" id="alumniMembersLearnMore">
                    <h6 class="learnMoreBox"><a href="/alumni" class="hover-highlight-animation">Learn More</a></h6>
                </div>
            </h13>

            <div class="socialMediaGrid">
                <div class="socialMedia1">
                <h7>
                    <a href="https://www.facebook.com/uclaaxs/" class="hover-underline-animation">FACEBOOK</a>
                </h7>
                </div>
                <div class="socialMedia2">
                    <h7>
                        <a href="https://www.instagram.com/uclaaxs/" class="hover-underline-animation">INSTAGRAM</a>
                    </h7>
                </div>
            </div>
        </>
    )
}