import React, {useEffect, useState} from 'react';

export default function Home()
{
    const [dropboxState, setDropboxState] = useState(false);
    const [dropbox, setDropbox] = useState(() => {
        return <div></div>;
    })

    useEffect(() => {
        console.log("USE EFFECT ", dropboxState)
        if (dropboxState)
        {
            setDropbox(<header>
                <nav>
                    <ul1>
                        <li><a href="/axs" class="hover-underline-animation">HOME</a></li>
                        <li><a href="/rush" class="hover-underline-animation">RUSH</a></li>
                        <li><a href="/people" class="hover-underline-animation">PEOPLE</a></li>
                        <li><a href="https://axe-beta-gamma.square.site/" class="hover-underline-animation">STORE</a></li>
                        <li><a href="https://my.setmore.com/bookingpage/97afc1fa-6977-4e61-98ee-293d35943cf1" class="hover-underline-animation">TUTORING</a></li>
                        <li><a href="/contact" class="hover-underline-animation">CONTACT</a></li>
                    </ul1>
                </nav>
            </header>)
        }
        else
        {
            setDropbox(<div></div>);
        }
    }, [dropboxState])

    function toggleDropboxState()
    {
        setDropboxState(!dropboxState);
    }
    return (
        <>
            <button onClick={toggleDropboxState} class="toggle-button">
                <span class="bar"></span>
                <span class="bar"></span>
                <span class="bar"></span>
            </button>
            {dropbox}
            <header class="navOverlayImage">
                <nav>
                    <ul>
                        <li class="loadInMovement"><a href="/axs" class="hover-underline-animation">HOME</a></li>
                        <li class="loadInMovement"><a href="/rush" class="hover-underline-animation">RUSH</a></li>
                        <li class="loadInMovement"><a href="/people" class="hover-underline-animation">PEOPLE</a></li>
                        <li class="loadInMovement"><a href="https://axe-beta-gamma.square.site/" class="hover-underline-animation">STORE</a></li>
                        <li class="loadInMovement"><a href="https://my.setmore.com/bookingpage/97afc1fa-6977-4e61-98ee-293d35943cf1" class="hover-underline-animation">TUTORING</a></li>
                        <li class="loadInMovement"><a href="/contact" class="hover-underline-animation">CONTACT</a></li>
                    </ul>
                </nav>
            </header>
            <img class="smallSize" id="wyvern" width="222" height="408" src="wyvernSmall.png" alt="Wyvern"/>
            <h1 class="smallSize">Alpha Chi Sigma</h1>
            <h2 class="smallSize">Beta Gamma Chapter</h2>
            <img id = "wyvern" src="wyvernSmall.png" alt="Wyvern"/>
            <div id="flame">
                <video width="970" height="545" autoPlay="autoPlay" muted>
                    <source src="flame.mp4" type="video/mp4"/>
                </video>
                <h1 class="flipNameCharacters">
                    <span>A</span>
                    <span>l</span>
                    <span>p</span>
                    <span>h</span>
                    <span>a</span>
                    <span>&nbsp;</span>
                    <span>C</span>
                    <span>h</span>
                    <span>i</span>
                </h1>
                <h1 class="flipNameCharacters" id="sigma">
                    <span>S</span>
                    <span>i</span>
                    <span>g</span>
                    <span>m</span>
                    <span>a</span>
                </h1>
                <h2>Beta Gamma Chapter</h2>
            </div>
            <h3>Our Purpose</h3>
            <div class="bsaGrid">
                <div class="bsa">
                    <img src="bind.png" width="200" height="231" alt="To Bind"/>
                        <h4>To Bind</h4>
                </div>
                <div class="bsa">
                    <img src="strive.png" width="200" height="231" alt="To Strive"/>
                    <h4>To Strive</h4>
                </div>
                <div class="bsa">
                    <img src="aid.png" width="200" height="231" alt="To Aid"/>
                    <h4>To Aid</h4>
                </div>
            </div>
            <div class="learnMoreOutline" id="shiftRight">
                <h6 class="learnMoreBox"><a href="/objects" class="hover-highlight-animation">Learn More</a></h6>
            </div>
            <div class="historyAndActivityGrid">
                <div class="historyAndActivity">
                    <img class="shrinkSize" src="Founders-Collage.jpg" alt="Founders"/>
                    <h5>HISTORY</h5>
                    <div class="learnMoreOutline">
                        <h6 class="learnMoreBox"><a href="/history" class="hover-highlight-animation">Learn More</a></h6>
                    </div>
                </div>
                <div class="historyAndActivity">
                    <img class="shrinkSize" src="BruinDay.jpg" alt="BruinDay"/>
                    <h5>ACTIVITIES</h5>
                    <div class="learnMoreOutline">
                        <h6 class="learnMoreBox"><a href="/activities" class="hover-highlight-animation">Learn More</a></h6>
                    </div>
                </div>
            </div>
            <div class="socialMediaGrid">
                <div class="socialMedia1">
                <h7>
                    <a href="https://www.facebook.com/uclaaxs/" class="hover-underline-animation">FACEBOOK</a>
                </h7>
                </div>
                <div class="socialMedia2">
                    <h7>
                        <a href="https://www.instagram.com/uclaaxs/" class="hover-underline-animation">INSTAGRAM</a>
                    </h7>
                </div>
            </div>
        </>
    )
   //return null;
   //https://css-tricks.com/scroll-triggered-animation-vanilla-javascript/
}