/*<h7 class="backLinkPeople"><a href="javascript:history.back()" class="whiteLink">&#10094; BACK TO PEOPLE</a></h7>*/

export default function Alumni()
{
    return (
        <>
            <div className="centerAll">
                <h7 class="backLinkPeople"><a href="javascript:history.back()" class="whiteLink">&#10094; BACK TO PEOPLE</a></h7>
                <img class="imageLoadDelay" id="alumniMembersPage" src="289458670_1037231557165581_8342686701647794390_n.jpg" alt="Alumni Members"/>
                <h14>Alumni</h14>
                <br></br><br></br>
                <p14>The following UCLA graduates were members of our Chapter.</p14>
                <div class="nameGrid">
                    {alumni.map((names) => (
                        <div class="name">
                            <Names name={names.name}/>
                        </div>
                    ))}
                </div>
                <p14>If you are an alumnus of our chapter, please contact the Webmaster to be added to the Facebook 
                Group and this list. The active members of the Beta Gamma Chapter look foward to every opportunity to meet any alumni.</p14>
                <div class="socialMediaGrid" id="socialMediaGridExtraSpace">
                    <div class="socialMedia1">
                    <h7>
                        <a href="https://www.facebook.com/uclaaxs/" class="hover-underline-animation">FACEBOOK</a>
                    </h7>
                    </div>
                    <div class="socialMedia2">
                        <h7>
                            <a href="https://www.instagram.com/uclaaxs/" class="hover-underline-animation">INSTAGRAM</a>
                        </h7>
                    </div>
                </div>
            </div>
        </>
    )
}