/*<h7 class="backLinkPeople"><a href="javascript:history.back()" class="whiteLink">&#10094; BACK TO PEOPLE</a></h7>*/

export default function Professor()
{
    return (
        <>
            <div className="centerAll">
                <h7 class="backLinkPeople"><a href="javascript:history.back()" class="whiteLink">&#10094; BACK TO PEOPLE</a></h7>
                <img class="imageLoadDelay" id="professorMembersPage" src="_DSC0486.jpg" alt="Professor Members"/>
                <h14>Professor Members</h14>
                <br></br><br></br>
                <p14>Our chapter has had the honor of having many UCLA Department of Chemistry and Biochemistry faculty members of Alpha Chi Sigma. The following are current or past faculty members.</p14>
                <div class="nameGrid" id="nameGridLessSpace">
                    {facultyMembers.map((names) => (
                        <div class="name">
                            <Names name={names.name}/>
                        </div>
                    ))}
                </div>
                <p14 id="professorParagraph">* deceased</p14>
                <p14 id="professorParagraph">** emeritus</p14>
                <div class="socialMediaGrid" id="socialMediaGridExtraSpace">
                    <div class="socialMedia1">
                    <h7>
                        <a href="https://www.facebook.com/uclaaxs/" class="hover-underline-animation">FACEBOOK</a>
                    </h7>
                    </div>
                    <div class="socialMedia2">
                        <h7>
                            <a href="https://www.instagram.com/uclaaxs/" class="hover-underline-animation">INSTAGRAM</a>
                        </h7>
                    </div>
                </div>
            </div>
        </>
    )
}