function nameArrangement(nameList)
{
    return (
        <div class="spacer">
            {nameList.map((names) => (
                <div class="name">
                    <Names name={names.name}/>
                </div>
            ))}
        </div>
    )
}

/*<h7 class="backLinkPeople"><a href="javascript:history.back()" class="whiteLink">&#10094; BACK TO PEOPLE</a></h7>*/

export default function Pledge()
{
    return (
        <>
            <div className="centerAll">
                <h7 class="backLinkPeople"><a href="javascript:history.back()" class="whiteLink">&#10094; BACK TO PEOPLE</a></h7>
                <img class="imageLoadDelay" id="pledgeClassPage" src="IMG_1611.jpg" alt="Pledge Class"/>
                <h14>Pledge Class</h14>
                <br></br><br></br>
                <p14>The following people all successfully completed the pledge process.</p14>
                <div class="nameGrid" id="nameGridLessSpace2">
                    {nameArrangement(f22)}
                    {nameArrangement(s22)}
                    {nameArrangement(f21)}
                    {nameArrangement(s21)}
                    {nameArrangement(w21)}
                    {nameArrangement(f19)}
                    {nameArrangement(s19)}
                    {nameArrangement(f18)}
                    {nameArrangement(s18)}
                    {nameArrangement(f17)}
                    {nameArrangement(s17)}
                    {nameArrangement(f16)}
                    {nameArrangement(s16)}
                    {nameArrangement(f15)}
                    {nameArrangement(s15)}
                    {nameArrangement(f14)}
                    {nameArrangement(s14)}
                    {nameArrangement(f13)}
                </div>
                <div class="socialMediaGrid">
                    <div class="socialMedia1">
                    <h7>
                        <a href="https://www.facebook.com/uclaaxs/" class="hover-underline-animation">FACEBOOK</a>
                    </h7>
                    </div>
                    <div class="socialMedia2">
                        <h7>
                            <a href="https://www.instagram.com/uclaaxs/" class="hover-underline-animation">INSTAGRAM</a>
                        </h7>
                    </div>
                </div>
            </div>
        </>
    )
}